.promo {
  position: relative;
  max-width: 1440px;
  margin: 0 auto;

  display: flex;
  flex-direction: column;

  color: $white-color;
  overflow: hidden;
  background-color: $dark-color;

  transform-style: inherit;

  @media (min-width: $sm) {
    &::before {
      position: absolute;
      content: '';
      bottom: -50%;
      left: 20%;
      width: 494px;
      height: 494px;

      background-image: radial-gradient(
        50% 50% at 50% 50%,
        #1a61cb 76.56%,
        rgba(9, 39, 98, 0) 100%
      );
      filter: blur(162px);
    }
  }

  // @media (min-width: $xl) {
  //   &::after {
  //     content: '';
  //     position: absolute;
  //     z-index: 1;
  //     top: 10%;
  //     left: 0;
  //     width: 300px;
  //     height: 373px;
  //     @include sprite-image($p04);
  //     // background-image: url('../images/promo/04.jpg');
  //     background-size: contain;
  //     background-repeat: no-repeat;
  //   }
  // }

  & .container {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;
    width: 100%;
    max-width: 1440px;
    margin: 0;
    padding: 0 15px;

    // padding: 0 15px;
    padding-top: 24px;

    @media (min-width: $xs) {
      padding-left: 45px;
      padding-right: 45px;
    }
    @media (min-width: $sm) {
      flex-direction: row;
    }
    @media (min-width: $md) {
      // flex-direction: row;
    }
  }

  & .pic-0 {
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    width: 290px;
    height: 290px;
    overflow: hidden;
    transition: 0.3s;
    opacity: 0;
    animation: showElementPic2 3s forwards;

    @media (min-width: $xl) {
      display: block;

    }

    &:before {
      content: '';
      position: absolute;
      height: 0;
      top: 0;
      left: 0;
      width: 200%;
      padding-bottom: 100%;
      background-size: 490%;
      background-position-x: -1480px;
      display: block;
      @include sprite-image($p04);
    }
  }

  &__info {
    width: 100%;
    z-index: 4;
    position: relative;
    margin-bottom: 40px;
    text-align: center;

    @media (min-width: $xs) {
      text-align: left;
    }

    @media (min-width: $sm) {
      padding-top: 62px;
    }
    @media (min-width: $xl) {
      padding-left: 230px;
      padding-top: 75px;
    }
  }

  &__title {
    max-width: 100%;
    margin-bottom: 15px;
    font-weight: 700;
    font-size: 18px;
    line-height: 130%;
    text-transform: uppercase;
    color: $primary-color;

    @include ff(
      'font-size',
      (
        320px: 18px,
        768px: 36px,
        1440px: 43px,
      )
    );

    @media (min-width: $sm) {
      line-height: 140%;
    }
    @media (min-width: $xl) {
      line-height: 110%;
    }
  }

  &__price {
    width: 100%;
    max-width: 100%;
    padding: 0;
    margin-bottom: 38px;
    font-weight: 400;
    font-size: 22px;
    line-height: 100%;
    text-transform: uppercase;

    @include ff(
      'font-size',
      (
        320px: 22px,
        768px: 36px,
        1200px: 52px,
      )
    );

    & span {
      @include ff(
        'font-size',
        (
          320px: 16px,
          768px: 26px,
          1200px: 32px,
        )
      );
    }
  }

  &__text {
    max-width: 100%;
    margin-bottom: 34px;
    font-weight: 700;
    font-size: 30px;
    line-height: 110%;
    text-transform: uppercase;

    @include ff(
      'font-size',
      (
        320px: 30px,
        1200px: 67px,
        1440px: 70px,
      )
    );

    @media (min-width: $sm) {
      line-height: 120%;
    }
  }

  &__pic {
    display: flex;
    position: relative;
    width: 100%;
    height: 100%;
    max-width: 350px;
    min-height: 348px;
    margin-left: auto;

    @media (min-width: $md) {
      width: 400px;
      min-height: 650px;
    }
    &--pic-w {
      position: absolute;
      bottom: -30px;
      right: -15%;
      width: 316.54px;
      height: 430px;

      @media (min-width: $xs) {
        width: 526px;
        height: 620px;
        bottom: -30px;
        right: -48%;
      }
      @media (min-width: $md) {
        width: 505px;
        height: 761px;
      }
      &::before {
        content: '';
        position: absolute;
        z-index: 3;
        bottom: 0;
        right: 10%;
        width: 100%;
        height: 100%;
        background-image: url('../images/promo/woman.webp');
        background-size: contain;
        background-repeat: no-repeat;
      }
    }
    &--pic-2 {
      position: absolute;
      bottom: 5%;
      left: 10%;
      width: 265.77px;
      height: 265.77px;

      @media (min-width: $xs) {
        width: 409px;
        height: 409px;
        left: -23%;
      }
      @media (min-width: $md) {
        width: 424px;
        height: 424px;
        left: -68%;
        bottom: 6%;
      }
      &::before {
        content: '';
        position: absolute;
        z-index: 0;
        bottom: 0;
        right: 0;
        width: 100%;
        height: 100%;
        background-image: url('../images/promo/ellipse2.webp');
        background-repeat: no-repeat;
        background-size: contain;
      }
    }

    &--pic-1 {
      position: absolute;
      top: -10%;
      right: -30%;

      width: 284.57px;
      height: 284.57px;

      @media (min-width: $xs) {
        width: 406px;
        height: 407px;
        top: -60%;
      }
      @media (min-width: $md) {
        width: 454px;
        height: 454px;
        right: -32px;
        top: -5%;
      }

      &::before {
        content: '';
        position: absolute;
        z-index: 0;
        bottom: 0;
        right: -10px;
        width: 100%;
        height: 100%;
        background-image: url('../images/promo/ellipse.webp');
        background-repeat: no-repeat;
        background-size: contain;
      }
    }
  }

  &__bg-images {
    @include sprite-image($woman);
    background-size: 400% 100%;
    background-repeat: no-repeat;
    width: 460px;
    height: 460px;
    max-width: 100%;
  }

  & .sprite-container {
    position: relative;
    width: 405px;
    height: 490px;
    overflow: hidden;

    &::before {
      content: '';
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 450%;
      height: 0;
      padding-bottom: 450%;
      background-position: -1197px 0;
      background-size: 200%;
      display: block;

      /* Set The Sprite as the Background */
      @include sprite-image($woman);
    }

    &::after {
      content: '';
      position: absolute;
      right: 0;
      background-position: 132px 0;
      width: 374%;
      bottom: 0;
      top: -123%;
      z-index: 1;
      height: 0;
      padding-bottom: 450%;
      background-size: 200%;
      display: block;

      /* Set The Sprite as the Background */
      @include sprite-image($circle);
    }
  }
}

.pic-container {
  position: relative;
  width: 500px;
  height: 100%;
  min-height: 300px;

  @media (min-width: 480px) {
    position: absolute;
    bottom: 0;
    right: 0;
    min-height: 560px;
  }

  & .pic-1,
  & .pic-2,
  & .pic-3 {
    position: relative;
    overflow: hidden;
    opacity: 0;
    transition: 0.3s;

    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 0;
      padding-bottom: 100%;
      background-size: 300% 100%;
      display: block;
    }
  }

  & .pic-1 {
    z-index: 2;
    width: 280px;
    height: 300px;
    margin-left: auto;
    animation: showElementPic1 2s forwards;

    &::before {
      @include sprite-image($woman);
      top: auto;
      bottom: 0;
      width: 172%;
      padding-bottom: 117%;
      background-size: 637%;
      background-position: -1200px 0px;
    }

    @media (min-width: 480px) {
      position: absolute;
      bottom: 0;
      right: 0;
      width: 320px;
      height: 560px;

      &:before {
        width: 160%;
        padding-bottom: 110%;
        background-size: 610%;
        background-position: -1200px 0;
      }
    }

    @media (min-width: $sm) {
      &:before {
        padding-bottom: 140%;
      }
    }

    @media (min-width: $md) {
      // position: relative;
      right: 0;
      bottom: 0;
      width: 373px;
      height: 560px;

      &:before {
        width: 170%;
        padding-bottom: 156%;
        background-size: 570%;
        background-position: -1400px 0;
      }
    }
    @media (min-width: $xl) {
      // right: -50px;
      right: 10%;
    }
  }

  & .pic-2 {
    position: absolute;
    left: 40%;
    width: 300px;
    height: 300px;
    bottom: -40%;
    animation: showElementPic2 3s forwards;

    animation-delay: 0.3s;

    &::before {
      @include sprite-image($circle);
      width: 180%;
      padding-bottom: 150%;
      background-size: 515% 190%;
      background-position-x: -153px;
    }

    @media (min-width: 480px) {
      left: 20%;
      bottom: -25%;

      &::before {
        background-size: 515% 190%;
      }
    }

    @media (min-width: $md) {
      &::before {
        background-size: 518% 190%;
      }
    }
    @media (min-width: $xl) {
      left: 7%;
      bottom: -10%;
      &::before {
        width: 190%;
        padding-bottom: 179%;
        background-size: 490% 190%;
        background-position-x: -160px;
      }
    }
  }
  & .pic-3 {
    position: absolute;
    top: 0;
    right: -10%;
    width: 280px;
    height: 280px;
    animation: showElementPic3 3s forwards;
    animation-delay: 0.6s;
    &::before {
      @include sprite-image($ellipse2);
      width: 200%;
      padding-bottom: 135%;
      background-size: 495% 220%;
      background-position-x: -452px;
    }

    @media (min-width: $xl) {
      right: 0;
    }
  }
}

