@keyframes showElement {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes showElementPic1 {
  from {
    opacity: 0;
    transform: translateY(10%);
  }

  to {
    opacity: 1;
    transform: translateY(0%);
  }
}
@keyframes showElementPic2 {
  from {
    opacity: 0;
    transform: translateX(10%);
  }

  to {
    opacity: 1;
    transform: translateX(0%);
  }
}
@keyframes showElementPic3 {
  from {
    opacity: 0;
    transform: translateX(-10%);
  }

  to {
    opacity: 1;
    transform: translateX(0%);
  }
}
